.subtitle {
  color: #000;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: -0.04em;
  margin: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid black;
}

@media screen and (max-width: 560px) {
  .subtitle {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 28px;
  }
}
